<template>
  <div id="swiper">
    <swiper
        :modules="modules"
        :slides-per-view="1"
        :space-between="50"
        navigation
        :pagination="{ clickable: true }"
        :scrollbar="{ draggable: true }"
        class="swiper"
    >
      <swiper-slide v-for="(img,index) in imgs" :key="index"><img :src=img.pic></swiper-slide>

    </swiper>
  </div>
</template>

<script>
import {getBanner} from '@/api'
import {Pagination, Scrollbar, A11y} from 'swiper';
import {Swiper, SwiperSlide} from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

export default {
  name: "Swiper_com",
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      imgs: [
        {pic: require("@/imgs/1.jpg")},
        {pic: require("@/imgs/2.jpg")},
        {pic: require("@/imgs/1.jpg")},
        {pic: require("@/imgs/2.jpg")},
        {pic: require("@/imgs/1.jpg")},
        {pic: require("@/imgs/2.jpg")},
      ]
    }
  },
  setup() {

    return {
      modules: [Pagination, Scrollbar, A11y],
    }
  },
  async mounted() {
    let res = await getBanner(2)
    this.imgs = res.data.banners
    // console.log(this.imgs)
  }

}
</script>

<style lang="less" scoped>
#swiper{
  //width: 7rem;
  .swiper {
  //height: 5rem;
  border-radius: 10px;

  img {
    width: 100%;
    height: 100%;
  }
}
}

</style>