<template>
    <div>
      手机号:<input type="text" v-model="phone">
      <br>
      密码:<input type="password" v-model="pwd">
      <button @click="loginEvent">登录</button>
    </div>
</template>

<script>
export default {
  name: "Login_com",
  data(){
    return{
      phone:"",
      pwd:"",
    }
  },
  methods:{
    async loginEvent() {
      let result = await this.$store.dispatch('login', {phone: this.phone, password: this.pwd})
      // console.log(result)
      if (result.data.code === 200) {
          this.$router.push("/me")
        }
    }
  }
}
</script>

