<template>
  <div class="playController">
    <div class="left" @click="show=!show">
      <img :src="playlist[playCurrentIndex].al.picUrl" alt="">
      <div class="content">
        <div class="title">
          {{ playlist[playCurrentIndex].name }}
        </div>
        <div class="tips">
          横划可以切换上下首
        </div>
      </div>
    </div>
    <div class="right">
      <svg class="icon" aria-hidden="true" v-show="!paused" @click="play">
        <use xlink:href="#music-bofang"></use>
      </svg>
      <svg class="icon" aria-hidden="true" v-show="paused" @click="play">
        <use xlink:href="#music-iconstop"></use>
      </svg>
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#music-liebiao"></use>
      </svg>
    </div>
    <playMusic @back="show = !show" :show="show" :play="play" :paused="paused" v-show="show" :playDetail="playlist[playCurrentIndex]"></playMusic>
    <audio ref="audio" :src="`https://music.163.com/song/media/outer/url?id=${playlist[playCurrentIndex].id}.mp3`"></audio>
  </div>
</template>

<script>
// import {mapState} from "vuex";
import {ref, computed} from "vue";
import store from "@/store";
import playMusic from "@/components/playMusic";

export default {
  name: "playController_com",
  components: {playMusic},
  data() {
    return {
      show: false
    }
  },
  setup() {
    const paused = ref(false)

    const playlist = computed(() => store.state.playlist);
    const playCurrentIndex = computed(() => store.state.playCurrentIndex);


    return {
      paused,
      playlist,
      playCurrentIndex
    }
  },
  computed: {

    // ...mapMutations(['playCurrentIndex'])
  },
  mounted() {
    console.log(this.$refs.audio)
    this.$store.dispatch('reqLyric', {id: this.playlist[this.playCurrentIndex].id})

  },
  updated() {
    this.$store.dispatch('reqLyric', {id: this.playlist[this.playCurrentIndex].id})

  },
  methods: {
    play() {
      if (this.$refs.audio.paused) {
        this.$refs.audio.play()
        this.paused = false
        this.updateTime()
      } else {
        this.$refs.audio.pause()
        this.paused = true
        clearInterval(this.$store.state.intervalId)
      }

    },

    updateTime() {
      this.$store.state.intervalId = setInterval(() => {
        this.$store.commit('setCurrentTime', this.$refs.audio.currentTime)
      }, 1000)
    }


  }
  ,
  watch: {
    playCurrentIndex() {
      this.isplay = false

    }
  }
}
</script>

<style lang="less" scoped>
.icon {
  width: 0.5rem;
  height: 0.5rem;
  fill: black;
}

.playController {
  width: 7.5rem;
  //padding: 0 0.5rem 0 0.5rem;
  //height: 1.2rem;
  position: fixed;
  //left: 0;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  border-radius: 0.3rem 0.3rem 0 0;
  margin: auto;
  border-top: 1px solid gray;

  .left {
    width: 3rem;
    height: 1rem;
    display: flex;
    align-items: center;
    padding-left: 0.5rem;

    img {
      width: 0.8rem;
      height: 0.8rem;
      border-radius: 0.5rem;
    }

    .content {
      padding-left: 0.1rem;

      .title {
        font-size: 0.2rem;
        font-weight: bold;
      }

    }


  }

  .right {
    padding-right: 0.4rem;
  }
}


</style>