import {createStore} from "vuex";
import api from "@/api"

const store = createStore({
    state() {
        return {
            playlist: [{al: {}}],
            playCurrentIndex: 0,
            lyric: "",
            currentTime: 0,
            intervalId: 0,
            user: {
                islogin: false,
                username: "未登录",

            }
        }

    },
    getters: {
        lyricList(state) {
            let arr = state.lyric.split(/\n/igs).map((item) => {
                let min = parseInt(item.slice(1, 3));
                let sec = parseInt(item.slice(4, 6));
                let mill = parseInt(item.slice(7, 11));


                return {
                    min, sec, mill,
                    lyric: item.slice(12, item.length),
                    content: item,
                    time: mill + sec * 1000 + min * 60 * 1000
                }

            })

            arr.forEach((item, i) => {
                if (i === 0) {
                    item.pre = 0;
                } else {
                    item.pre = arr[i - 1].time
                }

            })
            // console.log(arr)
            return arr

        }
    },
    mutations: {
        setPlayList(state, value) {
            state.playlist = value
        },
        setPlayIndex(state, value) {
            state.playCurrentIndex = value
        },
        setLyric(state, value) {
            state.lyric = value
        },
        setCurrentTime(state, value) {
            state.currentTime = value
        },
        setUser(state, value) {
            state.user.islogin = value
        }
    },
    actions: {
        async reqLyric(content, payload) {
            let res = await api.getLyric(payload.id)
            content.commit('setLyric', res.data.lrc.lyric)
            console.log(res.data.lrc.lyric)
        },

        async login(content, payload) {
            let result = await api.phoneLogin(payload.phone, payload.password)
            if (result.data.code === 200) {
                content.state.user.islogin = true
                // content.commit("setUser",content.state.user)

            }
            return result
        }

    }
})

export default store
