import axios from 'axios'

let base_url = "music.eleuu.com"
//获取轮播图api
export function getBanner(type = 0){
    let url = `${base_url}/banner?type=${type}`
    return  axios.get(url)

}
//获取每日推荐歌单
export function getRecommend(){
    let url = `${base_url}/personalized?limit=20`

    return axios.get(url)
}

//获取歌单详情
export function getListDetail(id){
    let url = `${base_url}/playlist/detail?id=${id}`

    return axios.get(url)
}

//获取歌词
export function getLyric(id){
    let url = `${base_url}/lyric?id=${id}`

    return axios.get(url)
}

//搜索歌曲内容
export function searchMusic(keyword){
    let url = `${base_url}/search?keyword=${keyword}`

    return axios.get(url)
}

//手机登录
export function phoneLogin(phone,password){
    let url = `${base_url}/login/cellphone?phone=${phone}&password=${password}`
    return axios.get(url)
}

export default {
    getBanner,
    getRecommend,
    getListDetail,
    getLyric,
    searchMusic,
    phoneLogin
}
