<template>
  <div class="musicList">
    <div class="musicList-top">
      <div class="title">发现好歌单</div>
      <div class="more">查看更多</div>
    </div>
    <div class="mlist">

      <swiper
          :modules="modules"
          :slides-per-view="3"
          :space-between="10"
          navigation
          class="swiper"
      >

        <swiper-slide class="swiper-slide" v-for="(recommend,index) in state.recommends" :key="index">
          <router-link :to="{path:'/list',query:{id:recommend.id}}">
            <div class="swiper-div">
              <div class="palyCount">
                <svg class="icon" aria-hidden="true">
                  <use xlink:href="#music-yunhang"></use>
                </svg>
                {{ changeValue(recommend.playCount) }}
              </div>
              <img :src="recommend.picUrl">
            </div>
            </router-link>
            <div>{{ recommend.name }}</div>


        </swiper-slide>

      </swiper>

    </div>
  </div>
</template>

<script>
import {Pagination} from 'swiper';
import {Swiper, SwiperSlide} from 'swiper/vue';
import {reactive, onMounted} from "vue";
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import {getRecommend} from "@/api";

export default {
  name: "musicList_com",
  data() {

  },
  components: {
    Swiper,
    // eslint-disable-next-line vue/no-unused-components
    SwiperSlide,
  },
  setup() {
    let state = reactive({recommends: []})

    function changeValue(num) {
      let res = 0
      if (num > 10000) {
        res = num / 10000
        res = res.toFixed(2) + "万"
      }
      return res
    }

    onMounted(async () => {
      let res = await getRecommend()
      state.recommends = res.data.result
      // console.log(state.recommends)
    })


    return {
      state,
      changeValue,
      modules: [Pagination],

    }
  },
  // async mounted() {
  //   let res = await getRecommend()
  //   this.recommends = res.data.result
  //   // console.log(this.recommends)
  // },


}
</script>

<style lang="less" scoped>
.icon {
  width: 0.3rem;
  height: 0.3rem;
  fill: white;

}

.musicList {
  width: 7.5rem;
  padding: 0 0.4rem;

  .musicList-top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.2rem;

    .title {
      font-size: 0.4rem;
      font-weight: 1500;
    }

    .more {
      border: 1px solid #ccc;
      padding: 0.1rem;
      border-radius: 0.5rem;
      text-align: center;

    }
  }

  .mlist {
    .swiper {

      .swiper-slide {
        text-align: left;


        .swiper-div {
          //width: 100px;
          //height: 100px;


          img {
            width: 100%;
            height: 100%;
            border-radius: 0.2rem;
          }

          .palyCount {
            position: absolute;
            right: 0.1rem;
            top: 0.1rem;
            font-size: 0.1rem;
            color: white;
            display: flex;
            align-items: center;
            //line-height:0.1rem;

          }


        }

      }
    }
  }
}
</style>