<template>
  <div class="playMusic">
    <div class="bg" :style="{backgroundImage:`url(${playDetail.al?.picUrl})`}"></div>
    <div class="playTop">
      <div class="back">
        <svg class="icon" aria-hidden="true" @click="$emit('back')">
          <use xlink:href="#music-zuojiantou"></use>
        </svg>
      </div>
      <div class="center">
        <div class="title">
          <marquee behavior="scroll" direction="left">{{ playDetail?.name }}</marquee>
        </div>
      </div>
      <div class="share">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#music-fenxiang"></use>
        </svg>
      </div>
    </div>
    <div class="playContent" v-show="!isLyric">
      <img class="needle" :class="{active:!paused}" src="@/imgs/needle-ab.png">
      <img class="disc" src="@/imgs/default_no.png">
      <img class="playImg" :src="playDetail.al?.picUrl">
    </div>
    <div class="playLyric" v-show="isLyric" ref="playLyric">
      <p :class="{active:( currentTime*1000 > item.pre && currentTime*1000 < item.time)}" v-for="(item,index) in $store.getters.lyricList" :key="index">{{ item.lyric }}</p>
    </div>

    <div class="progress">

    </div>
    <div class="playFooter">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#music-xunhuan"></use>
      </svg>
      <svg class="icon" aria-hidden="true" @click="goPlay(-1)">
        <use xlink:href="#music-shangyishoushangyige"></use>
      </svg>
      <svg v-show="!paused" class="icon" aria-hidden="true" @click="play">
        <use xlink:href="#music-iconstop"></use>
      </svg>
      <svg v-show="paused" class="icon" aria-hidden="true" @click="play">
        <use xlink:href="#music-bofang"></use>
      </svg>
      <svg class="icon" aria-hidden="true" @click="goPlay(1)">
        <use xlink:href="#music-xiayigexiayishou"></use>
      </svg>
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#music-liebiao"></use>
      </svg>

    </div>
  </div>
</template>

<script>
import {mapState} from "vuex";

export default {
  name: "playMusic",
  props: ["playDetail", "paused", "play"],
  data() {
    return {
      isLyric: true
    }
  },
  computed: {
    ...mapState(['playlist', 'playCurrentIndex', 'lyric', 'currentTime']),

  },
  methods:{
    goPlay(num){
      let index = this.playCurrentIndex + num
      if(index<0){
        index = this.playlist.length - 1
      }else if(index === this.playlist.length){
        index =0
      }
      this.$store.commit('setPlayIndex',index)
    }
  },
  watch: {
    currentTime() {
      let p = document.querySelector('p.active')
      if(p){
        this.$refs.playLyric.scrollTop = p.offsetTop;
      }
    }

  }
}
</script>

<style lang="less" scoped>

.playMusic {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: white;

  .bg {
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-size: auto 100%;
    background-position: center;
    filter: blur(60px);
    z-index: -1;
  }

  .playTop {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 7.5rem;
    height: 1.2rem;
    position: relative;
    //left: 0;
    //top: 0;
    color: white;
    margin: auto;
    //padding: 0 0.4rem;
    //margin-left: 0.2rem;

    .icon {
      width: 0.5rem;
      height: 0.5rem;
      fill: white;
    }

    .back {

    }

    .center {
      .title {
        marquee {
          font-size: large;
          width: 5rem;
          flex: 1;
        }
      }
    }
  }

  .playContent {
    position: absolute;
    width: 7.5rem;
    height: 3rem;
    left: 0;
    top: 1.5rem;

    .needle {
      width: 1.5rem;
      height: auto;
      position: absolute;
      left: 50%;
      transform-origin: 0.3rem 0;
      transform: rotate(-10deg);
      transition: all 1s;
      z-index: 10;
    }

    .needle.active {
      transform: rotate(30deg);
      transition: all 1s;
      z-index: 10;
    }

    .disc {
      width: 5.5rem;
      height: auto;
      position: absolute;
      left: calc(50% - 2.75rem);
      top: 1rem;
      z-index: 9;
    }

    .playImg {
      width: 3.5rem;
      height: 3.5rem;
      border-radius: 1.5rem;

      position: absolute;
      left: calc(50% - 1.7rem);
      top: 2rem;
      z-index: 8;
    }
  }

  .playFooter {
    width: 7.5rem;
    height: 1.5rem;
    position: absolute;
    //left: 0;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    //padding: 0 0.5rem;
    .icon {
      width: 0.5rem;
      height: 0.5rem;
      color: white;
    }
  }

  .playLyric {
    position: absolute;
    width: 7.5rem;
    height: 7.5rem;
    left: 0;
    //top: 1.5rem;
    top: calc(50% - 4rem);
    text-align: center;
    overflow: scroll;
    color: white;
    font-size: medium;

    .active {
      color: red;
    }
  }
}
</style>