<template>
  <ListViewTop_com :playlist="state.playlist" :creator="state.creator"></ListViewTop_com>
  <PlayList :playlist="state.playlist"></PlayList>
</template>

<script>

import {getListDetail} from "@/api";

import {onMounted, reactive} from "vue";
import {useRoute} from "vue-router";
import ListViewTop_com from "@/components/ListViewTop";
import PlayList from "@/components/playList";
import store from "@/store";
// import {useStore} from "vuex";

export default {
  name: "ListView_com",
  components: {PlayList, ListViewTop_com},
  setup() {
    const route = useRoute()
    let state = reactive({list: [], playlist: {}, creator: {}, tracks: []})

    onMounted(async () => {
      let id = route.query.id
      let res = await getListDetail(id)
      // console.log(res.data.playlist)

      state.playlist = res.data.playlist
      state.creator = res.data.playlist.creator
      state.tracks = res.data.playlist.tracks

      store.commit('setPlayList', state.tracks)

    })


    return {state}
  },
  mounted() {

  }

}
</script>

